import './Header.scss'
import React from 'react';
import { NavLink, Link } from 'react-router-dom'
import Content from '../../Localization/Content'

// import Component
import Profile from '../../Pages/Profile/Profile'

// import Hooks
import UseTheme from '../../Hooks/UseTheme'
import UseLang from '../../Hooks/UseLang'
import UseAuth from '../../Hooks/UseAuth'
import UseOpen from '../../Hooks/UseOpen'
import UseOpenMenu from '../../Hooks/UseOpenMenu';

// import Icons
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import VideoCallIcon from '@mui/icons-material/VideoCall';
import NotificationsIcon from '@mui/icons-material/Notifications';
import ViewComfyIcon from '@mui/icons-material/ViewComfy';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';

//Import Images
import LogoLight from '../../Images/logo-light.svg'
import LogoDark from '../../Images/logo-black.svg'

const Header = () => {

    //Import Hooks
    let [theme] = UseTheme()
    let [lang] = UseLang()
    let [token] = UseAuth()
    let [open, setOpen] = UseOpen()
    let [menubar, setMenubar] = UseOpenMenu()
    return (
        <>
        <header className={`header ${theme}`}>
            <div className="nav container">
               <div className="header-left">
                   <button className={`menu-bar ${theme}`}
                    onClick={()=> { menubar ? setMenubar(false) : setMenubar(true)}}>
                   <MenuIcon className="header-menu-bar" sx={{fontSize: '32px'}} />
                   </button>
                   <NavLink className='logo' to='/'>
                       <img src={theme === 'light' ? LogoLight : LogoDark } width='80' alt="logo" />
                    </NavLink>
                    <div className="input-search-block">
                        <input className="input-search" type="text" placeholder={Content[lang].search} />
                        <SearchIcon className="search-icon" sx={{color: '#333'}} />
                    </div>
               </div>
               <div className="header-right">
                    <VideoCallIcon className='icon-header' sx={{color: "#c2c2c2", marginRight: "30px", fontSize: '32px', cursor: 'pointer'}} />
                    <NotificationsIcon className='icon-header' sx={{color: "#c2c2c2", marginRight: "30px", fontSize: '32px', cursor: 'pointer'}} />
                    <ViewComfyIcon className='icon-header' sx={{color: "#c2c2c2", marginRight: "30px", fontSize: '32px', cursor: 'pointer'}} />

                    {token ? <button className="header-profile" onClick={()=>{ open ? setOpen(false) : setOpen(true)}}>
                        <img  src="https://yt3.ggpht.com/yti/APfAmoFz5_NXknTvVIEz-vXr56bCeWVh13WMPADI0GuUQw=s88-c-k-c0x00ffffff-no-rj-mo" alt="img" width='40' />
                    </button>  : <Link to='/login' className={`login-icon ${theme}`}><AccountCircleIcon  sx={{ fontSize: '30px', marginRight: '5px'}} />{Content[lang].login}</Link>
                    }
               </div>
              <Profile />
            </div>
        </header>  
        </>
    );
}

export default Header;
