import './Chanel.scss'
import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom'
import Content from '../../Localization/Content'

//import Data
// import DataVideo from '../../Data/Data'

import UseTheme from '../../Hooks/UseTheme'
import UseLang from '../../Hooks/UseLang'
import UseAuth from '../../Hooks/UseAuth'

//import Img 
import ChanelBgImg from '../../Images/chanelBgImg.png'

//import icon
// import NotificationsNoneOutlinedIcon from '@mui/icons-material/NotificationsNoneOutlined';
import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';


const Chanel = () => {

  let [theme] = UseTheme()
  let [lang] = UseLang()
  let [token] = UseAuth()

  let [state, setState] = useState([])
  let [chanel, setChanel] = useState([])
  let [stateFetch, setStateFetch] = useState([])

  let { id } = useParams()

  useEffect(() => {
      fetch('https://reqres.in/api/users?page=1')
      .then(res => res.json())
      .then(data => setState(data.data))

      fetch('https://reqres.in/api/users?page=2')
      .then(res => res.json())
      .then(data => setChanel(data.data))

      fetch('https://jsonplaceholder.typicode.com/photos')
      .then(res => res.json())
      .then(data => setStateFetch(data))
  }, []);
  
  return (
    <>
    
      <section className={`chanel container ${theme}`}>
          <img className='chanel-bg-img' src='https://picsum.photos/1200/300' alt="bg img" />
          {
            state.filter(find => find.id === Number(id)).map(user => {
              return(
                <div key={user.id} className="chanel-block">
                <img className='chanel-avatar' src={user.avatar} alt="" />
                <div className="description-block">
                  <div className="chanel-description">
                    <p className='chanel-name'>{user.first_name} {user.last_name}</p>
                    <span className='chanel-subscribed'>245k subscribed</span>
                  </div>
                  <button className='chanel-subscribe'>subscrid 2.3m</button>
                </div>
              </div>
              )
            })
          }
          <div className="chanel-menu">
            <Link to='/' className={theme}>
              Home
            </Link>
            <Link to='/' className={theme}>
              Videos
            </Link>
            <Link to='/' className={theme}>
              PlayLists
            </Link>
            <Link to='/' className={theme}>
              Channels
            </Link>
            <Link to='/' className={theme}>
              Discussion
            </Link>
            <Link to='/' className={theme}>
              About
            </Link>
            <div className="chanel-search">
            <SearchOutlinedIcon />
              <input className={theme} type="text"/>
            </div>
          </div>
          <div className="chanel-home">
            <div className="home-content">
              <img className='trend-content' src="https://picsum.photos/600/300" width='450' alt="" />
              <div className="home-content-description">
                <h2>Lorem ipsum dolor sit amet consectetur</h2>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ratione repudiandae rem mollitia commodi excepturi neque cumque similique sit, laborum quis ex praesentium amet dicta, consectetur architecto</p>
                <span>11 {Content[lang].views} • 6 {Content[lang].day}</span>
              </div>
            </div>
            <div className="recommended">
              <h2 className='recomended-title'>Recommendend chanel</h2>
              <ul>
              {
                chanel.map(users =>{
                  return (
                    <li className='users-item' key={users.id}>
                      <img className='users-avatar' src={users.avatar} alt="" width='60' />
                      <p className="name">{users.first_name} {users.last_name}</p>
                    </li>
                  )
                })
              }
              </ul>
            </div>
          </div>
          <div className="chanel-videos">
            {
            state.filter(f => f.id === Number(id)).map(user => {
                console.log(user.id);
                  return (
                    <>
                    <h2 className='chanel-name'>{user.first_name} {user.last_name} {Content[lang].videos}</h2>
                      <ul key={new Date().getTime()} className='video-list'>
                      {
                        stateFetch.filter(videos => videos.albumId === user.id).map(item => {
                          return (
                          <li key={item.id} className='video-item'>
                            <Link to={`/video/${item.id}`}>
                              <img src={item.url} alt="" width='400' />
                            </Link>
                            <p className='video-title' title={item.title}>{item.title}</p>
                              <div className="video-description">
                                <span>11{Content[lang].views}</span>
                                <span>{user.first_name} {user.last_name}</span>
                              </div>
                          </li>
                        )
                        })
                      }
                      </ul>
                    </>
                  )
              })
            }
          </div>   
      </section>
    </>
  );
}

export default Chanel;
