import './SidebarMenu.scss'
import React, { useState, useEffect, } from 'react';
import { NavLink, Link } from 'react-router-dom'

// import Hooks
import UseTheme from '../../Hooks/UseTheme'
import UseLang from '../../Hooks/UseLang'
import UseAuth from '../../Hooks/UseAuth'
import UseOpenMenu from '../../Hooks/UseOpenMenu';

//import content
import Content from '../../Localization/Content'

//import Icon 
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined'
import WhatshotOutlinedIcon from '@mui/icons-material/WhatshotOutlined';
import SubscriptionsOutlinedIcon from '@mui/icons-material/SubscriptionsOutlined';
import FolderOutlinedIcon from '@mui/icons-material/FolderOutlined';
import AlarmOnOutlinedIcon from '@mui/icons-material/AlarmOnOutlined';
import StarBorderOutlinedIcon from '@mui/icons-material/StarBorderOutlined';
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
import LibraryMusicOutlinedIcon from '@mui/icons-material/LibraryMusicOutlined';
import SportsEsportsOutlinedIcon from '@mui/icons-material/SportsEsportsOutlined';
import ExpandMoreOutlinedIcon from '@mui/icons-material/ExpandMoreOutlined';
import SettingsOutlinedIcon from '@mui/icons-material/SettingsOutlined';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';

const Sidebarmenu = () => {

    // import Hooks
    let [theme] = UseTheme()
    let [lang] = UseLang()
    let [token] = UseAuth()
    let [menubar, setMenubar] = UseOpenMenu()

    let [state, setState] = useState([])
    useEffect(() => {
        try{
        fetch('https://reqres.in/api/users?page=1')
            .then(res => res.json())
            .then(data => setState(data.data))
        }catch(error){
            console.log('error');
          }
    }, []);
    return (
        <>
        {
          menubar === true ? <section className={`sidebar ${theme}`}>
                <ul className="sidebar-menu">
                    <li className="menu-item">
                        <NavLink to='/'>
                            <HomeOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].home}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item">
                        <NavLink to='/trending'>
                            <WhatshotOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].trend}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item">
                        <NavLink to='/subscriptions'>
                            <SubscriptionsOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].subscription}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item">
                        <NavLink to='/library'>
                        <FolderOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].library}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item item-mobile">
                        <NavLink to='/history'>
                        <AlarmOnOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].history}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item item-mobile">
                        <NavLink to='/watch'>
                        <StarBorderOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].watch}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item item-mobile">
                        <NavLink to='/favourites'>
                        <FavoriteBorderOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].favourites}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item item-mobile">
                        <NavLink to='/liked'>
                        <LibraryMusicOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].like}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item item-mobile">
                        <NavLink to='/music'>
                        <SportsEsportsOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].music}</span>
                        </NavLink>
                    </li>
                    <li className="menu-item item-mobile">
                        <NavLink to='/games'>
                        <ExpandMoreOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].games}</span>
                        </NavLink>
                    </li>
                </ul>

                <h2 className="sidebar-title item-mobile">{Content[lang].subscription}</h2>
                <ul className="sidebar-users item-mobile">
                    {
                       token ? state.map((item) => {
                            return (
                                <li key={item.id} className="users-item  item-mobile">
                                    <img className="users-img" src={item.avatar} width='40' alt="user img" />
                                    <NavLink to={`/chanel/${item.id}`} >{item.first_name} {item.last_name}</NavLink>
                                </li> 
                            )
                        }) : <Link to='/login' className='login-icon'><AccountCircleIcon  sx={{color: "#c2c2c2", fontSize: '30px', marginRight: '5px'}} />{Content[lang].login}</Link>
                    }
                </ul>
                <ul className="sidebar-menu">
                    <li className="menu-item item-mobile">
                        <NavLink to='/settings'>
                            <SettingsOutlinedIcon sx={{theme}} />
                            <span>{Content[lang].setting}</span>
                        </NavLink>
                    </li>
                </ul>
            </section> : <section className={`bar-menu  ${theme}`}>
            <ul className={`menu`}>
                <li>
                    <NavLink className={`bar-item ${theme}`} to='/'>
                        <HomeOutlinedIcon sx={{fontSize: 22, theme}} />
                        <span>{Content[lang].home}</span>
                    </NavLink>
                </li>
                <li>
                    <NavLink className={`bar-item ${theme}`} to='/trending'>
                        <WhatshotOutlinedIcon sx={{fontSize: 22, theme}} />
                        <span>{Content[lang].trend}</span>
                    </NavLink>
                </li>
                <li>
                    <NavLink className={`bar-item ${theme}`} to='/subscriptions'>
                        <SubscriptionsOutlinedIcon sx={{fontSize: 22, theme}} />
                        <span>{Content[lang].subscription}</span>
                    </NavLink>
                </li>
                <li>
                    <NavLink className={`bar-item ${theme}`} to='/library'>
                    <FolderOutlinedIcon sx={{fontSize: 22, theme}} />
                        <span>{Content[lang].library}</span>
                    </NavLink>
                </li>
            </ul>
            </section>
        }
        </>
    );
}

export default Sidebarmenu;
