import './Settings.scss'
import React from 'react';
import Content from '../../Localization/Content'
// import Hooks
import UseTheme from '../../Hooks/UseTheme'
import UseLang from '../../Hooks/UseLang'

//import material UI
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select'

const Settings = () => {

    let [theme, setTheme] = UseTheme()
    let [lang, setLang] = UseLang()
    

    const themeChange = (event) => {
      setTheme(event.target.value);
    };
    const langChange = (event) => {
      setLang(event.target.value);
    };
    return (
      <>
      <div className="form-select">  
        <FormControl sx={{marginBottom: 3, minWidth: 320, backgroundColor: '#c2c2c2' }}>
          <Select
          value={theme}
          onChange={themeChange}
          displayEmpty
          inputProps={{ 'aria-label': 'Without label' }}>
            <MenuItem value='dark'>{Content[lang].themeDark}</MenuItem>
            <MenuItem value='light'>{Content[lang].themeLight}</MenuItem>
          </Select>
        </FormControl>  
        <FormControl sx={{marginBottom: 3, minWidth: 320, backgroundColor: '#c2c2c2' }}>
          <Select
          className='select-lang'
          value={lang}
          onChange={langChange}
          displayEmpty
          inputProps={{ 'aria-label': 'Without label' }}>
            <MenuItem value='uz'>Uzbekcha</MenuItem>
            <MenuItem value='eng'>English</MenuItem>
            <MenuItem value='ru'>Русский</MenuItem>
          </Select>
        </FormControl>  
      </div>
      </>
    );
}

export default Settings;
