import './Library.scss'
import React, {useState, useEffect} from 'react'
import {Link} from 'react-router-dom'
import Content from '../../Localization/Content'

// import Hooks
import UseLang from '../../Hooks/UseLang'
import UseAuth from '../../Hooks/UseAuth'

//import Icons
import AccountCircleIcon from '@mui/icons-material/AccountCircle';

function Library() {

    //import Hooks
    let [lang] = UseLang()
    let [token] = UseAuth()

    let [state, setState] = useState([])
    useEffect(() => {
        try{
        fetch('https://reqres.in/api/users?page=1')
            .then(res => res.json())
            .then(data => setState(data.data))
        }catch(error){
            console.log('error');
          }
    }, [state]);
    return (
        <>
        <section className="library-users container">
            {
            token ? <ul>
                {
                    state.map(users=> {
                        return (
                            <li key={users.id} className='library-users-item'>
                            <img className='users-img' src={users.avatar} width={60} alt="" />
                            <Link to={`/chanel/${users.id}`} className='users-name'>
                            {users.first_name} {users.last_name}
                            </Link>
                            </li>
                        )
                    }) 
                }
            </ul> : <Link to='/login' className='login-icon'><AccountCircleIcon  sx={{color: "#c2c2c2", fontSize: '44px'}} />{Content[lang].login}</Link>
            } 
        </section> 
        </>
    )
}

export default Library
