import './Profile.scss'
import React from 'react'
import {Link} from 'react-router-dom'
import Content from '../../Localization/Content'
import SettingsOutlinedIcon from '@mui/icons-material/SettingsOutlined';
import UseAuth from '../../Hooks/UseAuth'

import UseTheme from '../../Hooks/UseTheme'
import UseLang from '../../Hooks/UseLang'
import UseOpen from '../../Hooks/UseOpen'
import LogoutOutlinedIcon from '@mui/icons-material/LogoutOutlined';

function Profile() {

    // import Hooks
    let [open, setOpen] = UseOpen()
    let [theme] = UseTheme()
    let [lang] = UseLang()
    let [token, setToken] = UseAuth()
    return (
        <>
        <dialog 
         onClick={()=>{ open ? setOpen(false) : setOpen(true)}}
        className='profile-list' 
        open={open}>
         <div className="modal-content">
            <Link to='/settings' className='profile-item'>
                <SettingsOutlinedIcon sx={{theme}} />
                <span>{Content[lang].setting}</span>
            </Link>
            <button className='log-out' onClick={()=> setToken(false)}><LogoutOutlinedIcon sx={{marginRight: 1}} />Выйти</button>
         </div>
        </dialog>  
        </>
    )
}

export default Profile
