import './Video.scss'
import React from 'react';
import { useParams, Link} from 'react-router-dom'
import { useState, useEffect } from 'react'
import Content from '../../Localization/Content'

//import data
// import DataVideo from '../../Data/Data'

//import Hooks
import UseTheme from '../../Hooks/UseTheme'
import UseLang from '../../Hooks/UseLang'
import UseAuth from '../../Hooks/UseAuth'

// import icon
import ThumbUpOutlinedIcon from '@mui/icons-material/ThumbUpOutlined';
import ThumbDownAltOutlinedIcon from '@mui/icons-material/ThumbDownAltOutlined';
import ReplyOutlinedIcon from '@mui/icons-material/ReplyOutlined';
import Switch from '@mui/material/Switch';

const Video = () => {

    let [theme] = UseTheme()
    let [lang] = UseLang()
    let [token] = UseAuth() 

    let { id } = useParams()

    let [state, setState] = useState([])
    let [user, setUser] = useState([])

    useEffect(() => {
        try{
        fetch(`https://reqres.in/api/users?page=${token ? 1 : 2}`)
            .then(res => res.json())
            .then(data => setUser(data.data))
        }catch(error){
            console.log('error');
          }
        try{
        fetch('https://jsonplaceholder.typicode.com/photos')
            .then(res => res.json())
            .then(data => setState(data))
        }catch(error){
            console.log('error');
          }
    }, []);
    return (
        <>
            <section className='video container'>
                <div className="video-screen">
                    <div className="video-content">
                        {
                        state.filter(f => f.id === Number(id)).map(video =>{
                            return (
                            <>
                                <img className='screen' src={video.url} width='850' height='450' alt="" />
                                <p title={video.title} className='video-title'>{video.title}</p>
                                <div className="description-video">
                                    <span>123{Content[lang].views}</span>
                                    <div className='like-share'>
                                        <button className='icon'><ThumbUpOutlinedIcon sx={{ color: "#ccc", fontSize: '16px'}} /> 123k</button>
                                        <button className='icon'><ThumbDownAltOutlinedIcon sx={{ color: "#ccc", fontSize: '16px'}} /> 435k</button>
                                        <button className='icon'><ReplyOutlinedIcon sx={{ color: "#ccc", fontSize: '16px'}} /> {Content[lang].share}</button>
                                        <span>•••</span>
                                    </div>
                                </div>
                            {
                                user.filter( f=> f.id === video.albumId).map(item =>{
                                    return (
                                    <div className="chanel-description">
                                        <img className='chanel-avatar' src={item.avatar} width='60' alt="" />
                                        <div className="chanel-title">
                                            <h2 className='chanel-name'>{item.first_name} {item.last_name}</h2>
                                            <span className='chanel-publish'>Published on 14 Jun 2019</span>
                                            <p className='chanel-subtitle'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Praesentium ipsa non sunt! Perferendis consequuntur nesciunt et, expedita suscipit atque fuga? Quos incidunt in delectus ex dolore, eveniet veritatis, nam id dolor natus amet at repudiandae nesciunt accusantium iusto laborum aut?</p>
                                            <button className='btn-show'>{Content[lang].showMore}</button>
                                        </div>
                                        <button className='chanel-subscribe'>{Content[lang].subscribe} 2.3m</button>
                                    </div>
                                    )
                                })
                            }
                            </>
                            )
                        })
                        }
                    </div>
                    <ul className="recomemded-video">
                        {
                            state.filter(f => f.id <= 10).map(item=>{
                                return(
                                    <li key={item.id} className='video-item'>
                                        <img className='video-screen' src={item.url} width={300} alt="" />
                                        <p className={`video-title ${theme}`} title={item.title}>{item.title}</p>
                                        <span className='video-views'>123{Content[lang].views}</span>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </section>
        </>
    );
}

export default Video;
