import './Home.scss'
import React from 'react';
import { useState, useEffect, memo } from 'react';
import { Link } from 'react-router-dom'
import Content from '../../Localization/Content'

//import data
// import dataVideo from '../../Data/Data'

// import Hooks
import UseTheme from '../../Hooks/UseTheme'
import UseLang from '../../Hooks/UseLang'
import UseAuth from '../../Hooks/UseAuth'; 


const Home = () => {

  let [theme] = UseTheme()
  let [lang] = UseLang()
  let [token, setToken] = UseAuth()

  let [state, setState] = useState([])
  let [stateFetch, setStateFetch] = useState([])

  useEffect(() => {
    try{
    fetch(`https://reqres.in/api/users?page=${token ? 1 : 2}`)
      .then(res => res.json())
      .then(data => setState(data.data))
    }catch(error){
      alert('error');
    }
  }, [state]);
  useEffect(() => {
      fetch('https://jsonplaceholder.typicode.com/photos')
        .then(res => res.json())
        .then(data => setStateFetch(data))
  }, [stateFetch]);
  return (
    <section className={`home container ${theme}`}>
      <ul className="home-user">
        {
         state.map((item) => {
            return (
              <li key={item.id}>
                <Link className="home-user-link" to={`/chanel/${item.id}`}>
                  <img className="home-user-img" src={item.avatar} width="60" alt="img" />
                  <h2 className={`home-user-title ${theme}`}>{item.first_name} {item.last_name}</h2>
                </Link>
                <ul className='user-content'>
                  {
                    stateFetch.filter(album => album.albumId === item.id).map(alBum => {
                      return (
                          <li className="user-content-item" key={alBum.id}>
                            <Link to={`/video/${alBum.id}`}>
                                <img className='user-video' src={alBum.url} width='260' height='160' alt='' />
                                <p className={`subtitle ${theme}`}>{alBum.title}</p>
                            </Link>
                            <div className="description">
                              <span>80 {Content[lang].views} </span>
                              <span> · 3 {Content[lang].day}</span>
                              <span>{item.first_name} {item.last_name}</span>
                            </div>
                          </li>
                      )
                    })
                  }
                </ul>
              </li>
            )
          })
        }
      </ul>
    </section>
  );
}


export default memo(Home);
